import React, { useMemo } from "react";
import ListItem from "./ListItem";

import styles from "./List.module.css";

const List = (props) => {
  const { number } = props;
  const moneyPyramid = useMemo(
    () =>
      [
        { id: 1, amount: "$ 100" },
        { id: 2, amount: "$ 200" },
        { id: 3, amount: "$ 300" },
        { id: 4, amount: "$ 500" },
        { id: 5, amount: "$ 1.000" },
        { id: 6, amount: "$ 2.000" },
        { id: 7, amount: "$ 4.000" },
        { id: 8, amount: "$ 8.000" },
        { id: 9, amount: "$ 16.000" },
        { id: 10, amount: "$ 32.000" },
        { id: 11, amount: "$ 64.000" },
        { id: 12, amount: "$ 125.000" },
        { id: 13, amount: "$ 250.000" },
        { id: 14, amount: "$ 500.000" },
        { id: 15, amount: "$ 1.000.000" },
      ].reverse(),
    []
  );
  return (
    <ul className={styles.list}>
      {moneyPyramid.map((item) => {
        return (
          <ListItem
            key={item.id}
            id={item.id}
            style={
              number === item.id ? styles["list-active"] : styles["list-item"]
            }
            amount={item.amount}
          />
        );
      })}
    </ul>
  );
};

export default List;
